.profile__name {
  margin: 0;
  padding: 0;
  min-width: 294px;
  text-align: left;
  font-style: normal;
  font-weight: 500;
  font-size: 42px;
  line-height: 114%;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  color: #FFFFFF;
}


@media screen and (max-width: 380px) {
  .profile__name {
    min-width: 189px;
    font-size: 27px;
    line-height: 33px;
    text-align: center;
  }
}
